import React from 'react';
import { Link, Redirect } from 'react-router-dom';
import BreadCrumbs from '../../../Components/Material/BreadCrumbs';
import Ajax from '../../../utils/ajax';
import { isDev } from '../../../utils/env';
import { AppBar } from '../../../Components/Material/AppBar';
import { SideBar } from '../../../Components/Material/SideBar';
import { ContentArea } from '../../../Components/Material/ContentArea';
import { Paper } from '../../../Components/Material/Paper';
import { Typography } from '../../../Components/Material/Typography';
import { ListHeader, ListComponent, ListItemActions } from './Components/ListComponent';
import { Button } from '../../../Components/Material/Buttons';
import { MdAdd, MdDelete, MdEdit, MdPlayArrow } from 'react-icons/md';
import { IconButton } from '../../../Components/Material/Buttons/IconButton';
import { ListPagination } from './Components/ListPagination';
import { throwStatement } from '@babel/types';

class List extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            isLoading: true,
            redirectToCreate: false,
            projects: [],
            pagination: {
                total: 0,
                perPage: 0,
                currentPage: 0,
                lastPage: 0,
                from: 0,
                to: 0,
            },
        }

        this.redirectToCreate = this.redirectToCreate.bind(this);
        this.deleteProject = this.deleteProject.bind(this);
        this.fetchProjects = this.fetchProjects.bind(this);
        this.nextPage = this.nextPage.bind(this);
        this.previousPage = this.previousPage.bind(this);
    }

    redirectToCreate() {
        this.setState({redirectToCreate: true});
    }

    deleteProject(id) {
        if (!isDev()) {
            Ajax.delete(`${this.props.match.url}/${id}`)
                .catch(err => console.error(err));
        }

        this.setState({projects: this.state.projects.filter(p => p.id !== id)});
    }

    nextPage() {
        const { currentPage, lastPage } = this.state.pagination;
        if (currentPage >= lastPage) return;

        this.fetchProjects(currentPage + 1);
    }

    previousPage() {
        const { currentPage } = this.state.pagination;
        if (currentPage === 1) return;

        this.fetchProjects(currentPage - 1);
    }

    fetchProjects(pageNumber) {
        const url = process.env.NODE_ENV === 'development' ? `${process.env.PUBLIC_URL}/data/projects.json?page=${pageNumber}` : `${this.props.match.path}/?page=${pageNumber}`;
        
        Ajax.get(url)
            .then(res => {
                console.log(res);
                if (typeof res.projects !== undefined) {
                    this.setState({
                        isLoading: false,
                        projects: res.data,
                        pagination: {
                            total: res.total,
                            perPage: res.per_page,
                            currentPage: res.current_page,
                            lastPage: res.last_page,
                            from: res.from,
                            to: res.to,
                        }
                    });
                }
            })
            .catch(err => console.error(err));
    }

    componentDidMount() {
        this.fetchProjects(1);
    }

    render() {
        const { match } = this.props;
        return (
             <div className="grid-wrapper">
                <div className="topnav-area">
                    <AppBar />
                </div>
                <div className="sidebar-area">
                    <SideBar>
                        <ul>
                            <li>item 1</li>
                            <li>item 1</li>
                            <li>item 1</li>
                            <li>item 1</li>
                        </ul>
                    </SideBar>
                    
                
                </div>
                <div className="breadcrumbs-area" >
                    <BreadCrumbs links={[{ name: 'projects', path: '/projects'}]}/>
                </div>
                <div className="content-area">
                    <ContentArea centerContent={true}>
                        <ListHeader>
                            <Button color="primary" onClick={this.redirectToCreate}>
                                New Project <MdAdd/>
                            </Button>
                        </ListHeader>
                        { this.state.redirectToCreate ? <Redirect to={`${match.path}/create`} /> : '' }
                        { 
                            this.state.isLoading === true
                                ? 'Loading...'
                                :   <ListComponent style={{clear: 'both'}}>
                                    {
                                        this.state.projects.map(p => (
                                            <li key={p.id}>
                                                <Paper style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center'}}>
                                                    <Typography variant='subtitle1' style={{textTransform: 'capitalize'}}>{p.title}</Typography>
                                                    {/* <Link to={`${match.url}/${p.id}`} >{p.title}</Link> */}
                                                    <ListItemActions>
                                                        <Link to={`${match.url}/watch/${p.id}`} ><IconButton variant='text' ><MdPlayArrow size={25}/></IconButton></Link>
                                                        <Link to={`${match.url}/${p.id}`} ><IconButton variant='text'><MdEdit size={20} /></IconButton></Link>
                                                        <IconButton variant='text' onClick={() => this.deleteProject(p.id)}><MdDelete size={20}/></IconButton>
                                                    </ListItemActions>
                                                </Paper>
                                            </li>
                                        ))
                                    }
                                    <ListPagination {...this.state.pagination} next={this.nextPage} prev={this.previousPage}/>
                                    </ListComponent>
                        }
                        
                    </ContentArea>
                </div>
            </div>
        );    
    }
    
}

export default List;