import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { Typography } from '../../../../Components/Material/Typography';
import { MdKeyboardArrowLeft, MdKeyboardArrowRight } from 'react-icons/md';
import { theme } from '../../../../Components/Material/Variables';

const Container = styled.div`
    display: flex;
    justify-content: flex-end;

    div {
        display: flex;
        justify-content: space-between;
    }
`;

const PaginationButton = styled.button`
    display: flex;
    justify-content: center;
    align-items: center;
    width: ${props => props.size ? props.size : '30px'};
    height: ${props => props.size ? props.size : '30px'};
    box-sizing: border-box;
    color: ${props => props.disabled === true ? theme.palette.text.secondary : theme.palette.text.primary};
    transition: color 0.5s ease;
    background-color: transparent;
    transition: background-color 0.5s ease;
    border: none;
    border-radius: 50%;
    ${props => props.disabled ? '' : 'cursor: pointer;'}
    outline: none;

    &:hover {
        ${props => props.disabled ? '' : `background-color: ${theme.palette.grey['300']};`}
    }
`;


export const ListPagination = function(props) {

    const { total, perPage, currentPage, lastPage, from, to } = props;
    return (
        <Container>
            <div>
                <Typography variant='subtitle1'>{from}-{to} of {total}</Typography>
                <div>
                    <PaginationButton disabled={currentPage === 1} onClick={() => props.prev()}>
                        <MdKeyboardArrowLeft size={25}/>
                    </PaginationButton>
                    <PaginationButton disabled={currentPage === lastPage} onClick={() => props.next()}>
                        <MdKeyboardArrowRight size={25}/>
                    </PaginationButton>
                </div>
            </div>
        </Container>
    );
};

ListPagination.propTypes = {
    total: PropTypes.number.isRequired,
    perPage: PropTypes.number.isRequired,
    currentPage: PropTypes.number.isRequired,
    lastPage: PropTypes.number.isRequired,
    from: PropTypes.number.isRequired,
    to: PropTypes.number.isRequired,
};